<template>
  <div class="goodslist">
    <mt-header fixed title="全部商品"></mt-header>
    <div class="search">
        <!-- <input type="text" placeholder="搜索商品"> -->
        <!-- <span class="iconfont">&#xe62c;</span> -->
        <mt-search
        v-model="value"
        cancel-text="取消"
        placeholder="搜索">
        </mt-search>
    </div>
    <div class="goods_tabs">
        <div class="goods_tab">综合</div>
        <div class="goods_tab">销量</div>
        <div class="goods_tab">价格</div>
    </div>
    <!--  -->
    <div class="goods_list2">
      <div class="tuijian_goods">
        <img src="../assets/goods1.jpg" alt="">
        <p>【满口麻香】向妈妈年货大礼包组合</p>
        <div class="tuijian_price">
          <span class="price_ze">￥39.9</span>
          <span class="price_yuan">原价￥99</span>
        </div>
      </div>
      <div class="tuijian_goods">
        <img src="../assets/goods1.jpg" alt="">
        <p>【满口麻香】向妈妈年货大礼包组合</p>
        <div class="tuijian_price">
          <span class="price_ze">￥39.9</span>
          <span class="price_yuan">原价￥99</span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="goods_list2">
        <div class="tuijian_goods" v-for="(item,index) in goodslist">
            <img :src='item.url_da[0]' alt="">
            <p><router-link :to="{ name:'Detail' }">{{item.title}}</router-link></p>
            <div class="tuijian_price">
                <span class="price_ze">￥{{item.nowprice}}</span>
                <span class="price_yuan">原价￥{{item.price}}</span>
            </div>
        </div>
    </div>




    <!--  -->
    <!--  -->
    <div class="foot">
        <Footer :foottab='2'></Footer>
    </div>
  </div>
</template>

<script>
import Footer from './common/Footer.vue'
export default {
  components:{
    Footer
  },
  data () {
    return {
        // 搜索
        value:'',
        selected: 2,
        goodslist:[]
    }
  },
  created() {
        let url = 'static/data/goodsList.json';
        console.log(url)
        // this.$http.get("src/data/courseList.data")
        this.$http.get(url)
        .then(res=>{
          console.log(res)
          console.log(res.data.url_da)
          this.goodslist=res.data
        })
        .catch(function(err){
          console.log(err)
        })
  }

}
 
</script>

<style scoped>
    .goodslist >>> .mint-header{
        z-index: 10000;
    }
    .goodslist{
        /* padding-top: 40px; */
        /* padding-bottom: 50px; */
        padding: 40px 0px 50px;
    }
    /*  */
    .search{
        padding: 10px 10px 0;
        /* background-color: aquamarine; */
        position: relative;
    }
    .search input{
        outline: none;
        border: 1px solid #ccc;
        width: 100%;
        height: 25px;
        border-radius: 100px;
        padding-left: 10px;
        /* float: left; */
    }
    .search span{
        border-radius: 100px;
        /* border: 1px solid #ccc; */
        display: inline-block;
        padding: 0 10px;
        height: 25px;
        line-height: 25px;
        text-align: center;
        /* background-color: #26a2ff; */
        position: absolute;
        right: 10px;
    }

    .mint-search {
        height: 100%;
        /* height: 100vh; */
        overflow: hidden;
    }
    .search >>> .mint-searchbar {
        background-color: #ffffff;
    }
    .search >>> .mint-searchbar-inner{
        border: 1px solid #ccc;
        border-radius: 50px;
        height: 35px;
    }
    .search >>> .mint-searchbar-core{
        font-size: 0.75rem;
    }

/*  */
    .goods_tabs{
        /* 设置为弹性容器 */
        display: flex;
        /* 换行，第一行在上方 */
        flex-wrap: wrap;
        /* 间隔相等 ，自动分配距离*/
        justify-content: space-around;
        align-items: center; 
        border-radius: 5px;
    }
    .goods_tab{
        padding: 5px;
        font-size: 0.875rem;
        text-align: center;
        flex-grow: 1;
        border-bottom: 1px solid #f8f8f8;
    }
    .goods .active{
        color: blue;
    }

/*  */
.goods_list2{
        /* 设置为弹性容器 */
        display: flex;
        /* 换行，第一行在上方 */
        flex-wrap: wrap;
        /* 间隔相等 ，自动分配距离*/
        justify-content: flex-start;
        align-items: center; 
        border-radius: 5px;
        padding: 0 8px;
    }
  .tuijian_goods{
    box-shadow: 0px 0px 5px #ddd;
    border-radius: 10px;
    list-style-type: none;
    border: 1px solid rgb(232, 235, 233);
    float: left;
    /* margin: 50px 54px ; */
    /* background-color: teal; */
    flex-basis: 47%;
    margin: 5px;
    /* flex-grow: 1; */
    /* height: 150px; */
    /* background-color: deepskyblue; */
    border-radius: 5px;
    overflow: hidden;
  }
  .tuijian_goods p{
    font-size: 0.875rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .tuijian_goods p a{
    font-size: 0.875rem;
  }
  .tuijian_goods img{
    width: 100%;
    /* border-radius: 10px 10px 0 0; */
  }
  .tuijian_price{
    margin-top: 10px;
  }
  .tuijian_price .price_ze{
    font-size: 1.275rem;
    color: rgb(253, 114, 21);
  }
  .price_yuan{
    color: #444;
    color: rgb(178, 191, 216);
    font-size: 0.75rem;
  }


  .mint-tab-container-item{
      /* 设置为弹性容器 */
    display: flex;
    /* 换行，第一行在上方 */
    flex-wrap: wrap;
    /* 间隔相等 ，自动分配距离*/
    justify-content: space-around;
    align-items: center; 
    border-radius: 5px;
    margin-bottom: 50px;
  }



  .foot{
    width: 100%;
    height: 45px;
    background-color: rgb(255, 80, 80);
    position: fixed;
    bottom: 0;
    left: 0;
  }
</style>



